.splash {
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;

  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;

  background: url(../../images/2020PS-splash-bg.png);
  background-size: cover;

  @include color-content(white);

  .splash-container {
    display: flex;
    flex-direction: column;
    width: 100%;
    height: 100%;

    .splash-upper {
      display: flex;
      flex-direction: row;
      width: 100%;
      height: 100%;
      flex: 1;

      .splash-left-profile {
        width: 50%;
        height: 100%;

        display: flex;
        flex-direction: column;        

        background-color: rgba($brand-primary, 0.04);

        .splash-left-upper-container {
          display: flex;
          flex-direction: column;
          justify-content: center;
          align-items: center;
          flex: 1;

          .splash-left-upper {
            display: flex;
            flex-direction: column;
            align-items: center;
            justify-content: center;

            margin-top: 40px;

            .splash-profile-picture {
              text-align: center;
              position: relative;

              display: flex;
              justify-content: center;
              align-items: center;

              img {
                width: 210px;
                z-index: 1;
              }

              .splash-profile-anim {
                position: absolute;

                top: 50%;
                left: 50%;
                transform: translate(-50%, -50%);

                $splash-anim-funky-length: 75s;

                .splash-anim-funkyCircle {
                  animation: RotateFunkCircle1 $splash-anim-funky-length/3 linear infinite, DashFunkCircle1 $splash-anim-funky-length ease-in-out infinite;
                  transform-origin: 50% 50%;
                }

                .splash-anim-funkyCircle2 {
                  animation: RotateFunkCircle2 $splash-anim-funky-length/2 linear infinite, DashFunkCircle2 $splash-anim-funky-length ease-in-out infinite;
                  transform-origin: 50% 50%;
                }

                .splash-anim-funkyCircle3 {
                  animation: RotateFunkCircle3 $splash-anim-funky-length linear infinite, DashFunkCircle3 $splash-anim-funky-length ease-in-out infinite;
                  transform-origin: 50% 50%;
                }

                @mixin RotateFunkCircle($rotation-offset, $direction) {
                  0% { 
                    transform: rotate($rotation-offset + $direction*0deg);
                  }
                  100% { 
                    transform: rotate($rotation-offset + $direction*360deg);
                  }
                }

                @mixin DashFunkCircle($base-dash, $dasharray-mod) {
                  0% { 
                    stroke-dasharray: $base-dash;
                  }
                  33% { 
                    stroke-dasharray: $base-dash + $dasharray-mod;
                  }
                  66% { 
                    stroke-dasharray: $base-dash - $dasharray-mod;
                  }
                  100% { 
                    stroke-dasharray: $base-dash;
                  }
                }

                @keyframes RotateFunkCircle1 {
                  @include RotateFunkCircle(-90, 1);
                }
                @keyframes RotateFunkCircle2 {
                  @include RotateFunkCircle(0, -1);
                }
                @keyframes RotateFunkCircle3 {
                  @include RotateFunkCircle(90, 1);
                }

                @keyframes DashFunkCircle1 {
                  @include DashFunkCircle(1085, 0);
                }
                @keyframes DashFunkCircle2 {
                  @include DashFunkCircle(789, 40);
                }
                @keyframes DashFunkCircle3 {
                  @include DashFunkCircle(990, 60);
                }

                &.splash-anim-leftline {
                  transform: translate(calc(-50% - 195px), -50%);
                }
                &.splash-anim-rightline {
                  transform: translate(calc(-50% + 199px), -50%);
                }

              }
            }

            .splash-name {
              h1 {
                text-transform: uppercase;
                font-size: 3.2rem;
                font-weight: 700;
                margin-bottom: 0;
                margin-top: 90px;
                color: $brand-secondary;
              }
            }
            .splash-tagline {
              background-color: $brand-primary;
              padding: 8px 25px 6px 25px;
              span {
                text-transform: uppercase;
                font-weight: 900;
                font-size: 1rem;
                letter-spacing: 0.3rem;
                font-family: $font-big;

                color: #14120b;
              }
            }

          }
        }
        
        .splash-left-lower {
          display: flex;
          justify-content: center;
          margin-bottom: 20px;
        }
      }
      .splash-right-menu {
        width: 50%;

        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;
      }
    }
    .splash-lower-terminal-container {
      display: flex;
      flex-direction: row;
      justify-content: space-between;

      .splash-lower-left-terminal {
        width: calc(50% - 25px);
        background-color: rgba($brand-primary, 0.04);

        display: flex;
        flex-direction: row;
        align-items: center;

        padding-bottom: 15px;
        padding-left: 25px;


        .splash-lower-left-terminal-blinker {
          width: 6px;
          height: 14px;
          background-color: $brand-primary;
          margin-right: 10px;
        }

        span {
          margin-top: 1px;
          font-family: $font-heading;
          font-weight: 700;
          font-size: 9px;
          letter-spacing: 0.5px;
          color: $brand-primary;
        }
      }
    }
    .splash-lower-bar {
      width: 100%;
      height: 6px;
      background: $brand-primary;
    }
  }
}